import "./styles.css";

import histogramData from "./histogram.csv";
import histogramOpenFMData from "./histogram-openfm.csv";
import histogramFailedData from "./histogram-failed.csv";

import { Chart, registerables } from "chart.js";

Chart.register(...registerables);

function parseHisto(histogramData) {
  return histogramData
    .split("\n")
    .map((line) => line.replace(/"/g, "").split(", "))
    .map(([date, n]) => [
      date.replace(/^(\d{1,2})\.(\d{2})\.(\d{4}) /, "$3-$2-$1 "),
      Number(n)
    ]);
}

const data = parseHisto(histogramData);
const failedData = parseHisto(histogramFailedData);
const openFMData = parseHisto(histogramOpenFMData);

const daysOfWeek = ["Nd", "Pn", "Wt", "r", "Cz", "Pt", "Sb"];

function getDayOfWeek(date) {
  const dayIndex = new Date(date).getDay();

  return daysOfWeek[dayIndex];
}

const ctx = document.getElementById("myChart");

function createChart(beginDate, endDate) {
  const beginIndex = data.findIndex(([date]) => date === beginDate);
  const endIndex = data.findIndex(([date]) => date === endDate);

  const filteredData = data.filter(
    (_, index) => index >= beginIndex && index <= endIndex
  );

  const filteredFailedData = failedData.filter(
    (_, index) => index >= beginIndex && index <= endIndex
  );

  const filteredOpenFMData = openFMData.filter(
    (_, index) => index >= beginIndex && index <= endIndex
  );

  return new Chart(ctx, {
    type: "line",
    data: {
      labels: filteredData.map(([date]) => `${date} (${getDayOfWeek(date)})`),
      datasets: [
        {
          label: "Liczba uzytkownikw",
          data: filteredData.map(([, n]) => n),
          backgroundColor: "#00d",
          borderColor: "#00d",
          borderWidth: 1,
          pointRadius: 1
        },
        {
          label: "Liczba userek",
          data: filteredFailedData.map(([, n]) => n),
          backgroundColor: "#d00",
          borderColor: "#d00",
          borderWidth: 1,
          pointRadius: 1
        },
        {
          label: "Liczba przejsc na open.fm",
          data: filteredOpenFMData.map(([, n]) => n),
          backgroundColor: "#dd0",
          borderColor: "#dd0",
          borderWidth: 2,
          pointRadius: 2
        }
      ]
    },
    options: {
      scales: {
        y: {
          beginAtZero: true
        }
      }
    }
  });
}

let chart = createChart(data[0][0], data[data.length - 1][0]);

const beginDate = document.getElementById("begin-date");
const endDate = document.getElementById("end-date");

function updateRange() {
  chart.destroy();

  chart = createChart(beginDate.value, endDate.value);
}

function addOptions(select, options, value = null) {
  options.forEach((value) => {
    const option = document.createElement("option");
    option.textContent = value + " (" + getDayOfWeek(value) + ")";
    option.value = value;
    select.append(option);
  });
  select.value = value;
}

const datesList = data
  .filter((_, index) => index % 12 === 0)
  .map(([date]) => date);

addOptions(beginDate, datesList, datesList[0]);

addOptions(endDate, datesList, datesList[datesList.length - 1]);

beginDate.addEventListener("change", updateRange);
endDate.addEventListener("change", updateRange);
